@import url('https://fonts.googleapis.com/css2?family=Rubik&display=swap');

body {
    margin: 0;
    font-family: 'Rubik', sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;

    box-sizing: border-box;
}

.container {
    box-sizing: border-box;
    max-width: 1180px;
    width: 100%;
    margin: 0 auto;
    padding: 0 5px;
}

.layout {
    width: 100%;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    position: relative;
}


/*HEADER*/


.header {
    margin-top: 5px;
    border-bottom: 1px solid #aaa0a0;
    box-shadow: 2px 2px 2px rgba(0, 0, 0, 0.5);
}

.header .container,
.footer .container {
    margin: 0 auto;
    display: flex;
    justify-content: space-between;
    align-items: center;
    align-content: center;
}

.header .container {
    padding-bottom: 5px;
}

.header-login {
    cursor: pointer;
}

.header-logged {
    display: flex;
}

.header img {
    object-fit: contain;
    width: 50px;
    height: 50px;
}

.header-username {
    margin-right: 15px;
    margin-top: 1px;
}

.menu {
    max-width: 225px;
}

.menu span a,
.header-login a {
    color: #363d54;
    cursor: pointer;
    margin: 0 10px;
    text-decoration: none;
}

.menu span a.active,
.header-login a.active {
    color: #1fbae5;
}

.menu span a:hover,
.header-login a:hover {
    color: #ffbd04;
}

@media (max-width: 500px) {
    .header img {
        object-fit: contain;
        width: 25px;
        height: 25px;
    }

    .header-logo {
        width: 25px;
        height: 25px;
    }
}


/*FOOTER*/


.footer .container {
    margin: 25px auto 35px;
}

@media (max-width: 500px) {
    .footer .container {
        margin: 10px auto 15px;
        display: flex;
        flex-direction: column;
        text-align: center;
    }

    .footer .menu {
        margin-bottom: 10px;
    }
}

.footer {
    width: 100%;
    position: fixed;
    background: #e0d9d9;
    bottom: 0;
}

.spacer {
    margin-bottom: 92px;
}

.footer-text {
    max-width: 300px;
}

.contentTitle {
    background: #d7d7d7;
}


/*HOME*/


.contentTitle .container {
    margin: 60px auto;
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
}

.contentTitle h1 {
    font-size: 30px;
    margin-bottom: 15px;
}

.contentTitle-text {
    max-width: 540px;
    margin-bottom: 15px;
}

.button {
    background: #2187b6;
    color: white;
    border-radius: 15px;
    padding: 10px 20px;
    cursor: pointer;
    border: none;
}

.button:active {
    box-shadow: inset 2px 2px 2px rgba(0, 0, 0, 0.5);
}

.content .container {
    margin: 10px auto 0;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.content-cards {
    width: 100%;
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
}

.card {
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    max-width: 180px;
    border: 2px solid #bfb7af;
    box-shadow: 2px 2px 2px rgba(0, 0, 0, 0.5);
    margin: 0 0 10px 10px;
}

.card-items {
    padding: 20px 2px;
}

.card-logo {
    overflow: hidden;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 180px;
    width: 180px;
    border-bottom: 1px solid #bebebe;
}

.card-logo img {
    height: 100%;
    width: auto;
    object-fit: contain;
}

.card-name {
    font-size: 18px;
    font-weight: bold;
    color: #2187b6;
}

.card-description {
    font-size: 14px;
    margin-bottom: 15px;
}


/*ABOUT*/


.about .container {
    margin: 0 auto 15px;
}

.about h1 {
    text-align: center;
}

.about p {
    margin: 0 10px;
}

/*AUTH*/


.auth .container {
    margin: 0 auto;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.auth .container div {
    display: flex;
    justify-content: center;
    width: 100%;
    max-width: 400px;
    margin-top: 10px;
}

.auth a {
    text-decoration: none;
    color: white;
}

.auth button {
    min-width: 130px;
}

.auth a button {
    width: 90%;
}

.auth input {
    width: 100%;
    border-radius: 20px;
    padding: 10px 20px;
    outline: none;
    border: 1px solid #d7d0c7;
}

.auth-buttons > * {
    margin: 0 10px;
    width: 40%;
}

@media (max-width: 500px) {
    .auth button {
        padding: 10px 5px;
    }

}

/*ADMIN-SIDEBAR*/


.content-admin {
    flex: 1;
    display: flex;
}

.content-admin .container {
    flex: 1;
    display: flex;
}

.sidebar {
    display: flex;
    flex-direction: column;
    background: #2187b6;
    max-width: 170px;
    width: 45%;
}

.sidebar a {
    color: white;
    text-decoration: none;
    margin: 15px 0 0 15px;
}

@media (max-width: 500px) {
    .sidebar a {
        color: white;
        text-decoration: none;
        margin: 15px 5px 0 5px;
        text-align: center;
    }
}

.sidebar a.active {
    color: #26e51f;
}


.sidebar a:hover {
    color: #ffbd04;
}

/*CREATION FORM*/


.content-admin-creationform {
    margin: 15px 25px;
    width: 100%;
}

.content-admin-creationform label {
    display: block;
    margin: 10px 0 5px 0;
}

.content-admin-creationform input {
    outline: none;
    border: 1px solid #bebebe;
    width: 90%;
    font-size: 20px;
}

.creationform-footer-input {
    display: flex;
    justify-content: space-between;
}

.creationform-footer {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-top: 15px;
}

.creationform-footer label {
    margin: 0 10px 0 0;
}

#creationform-file,
#creationform-id {
    opacity: 0;
    visibility: hidden;
    position: absolute;
    width: 0;
}

.creationform-file {
    display: flex;
}

.creationform-file label {
    text-decoration: underline;
    color: #1fbae5;
    cursor: pointer;
    margin-left: 5px;
}

.creationform-file label:hover {
    color: #ffbd04;
}


.creationform-message.error {
    text-align: center;
    color: red;
    margin-top: 20px;
}

.creationform-message.success {
    text-align: center;
    color: #0547f1;
    margin-top: 20px;
}


@media (max-width: 880px) {
    .creationform-footer {
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        justify-content: space-between;
        margin-top: 15px;
    }

    .creationform-footer > * {
        margin-top: 15px;
    }

    .creationform-footer-input {
        display: block;
    }

    .creationform-footer-input label {
        margin: -15px 0 5px 0;
    }
}

/*EDITION FORM*/


.content-admin-editform {
    margin: 15px 25px;
    width: 100%;
}

.editform-card {
    box-sizing: border-box;
    height: 70px;
    border: 1px solid #eaeaea;
    border-radius: 2px;
    margin-bottom: 10px;
    padding: 5px;
    display: flex;
}

.editform-card-icon {
    width: 60px;
    height: 60px;
    display: flex;
    justify-content: center;
    align-items: center;
}

.editform-card-icon img {
    width: 100%;
    height: auto;
    max-height: 100%;
    object-fit: contain;
}

.editform-card-content {
    width: 100%;
    margin-left: 30px;
}

.editform-card-content p {
    margin: 3px 0 0 0;
    padding: 0;
}

.editform-card-content div {
    margin-top: 10px;
}

.editform-card-delete {
    margin-left: 10px;
    color: red;
    cursor: pointer;
}

.editform-card-delete:hover {
    color: #ffbd04;
}

.editform-card-edit {
    color: #077cea;
    cursor: pointer;
}

.editform-card-edit:hover {
    color: #ffbd04;
}